@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap");
:root {
  --pc: #ea3d28;
  --p-color: #061c3d;
  --bg-color: #fffceb;
  --footer-bg: #252525;
  --header-bg: #14131300;
  --gray-color: #6f6f6f;
  --white: #FEFEFE;
  --black: #000000;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 600;
  --font-sb: 700;
  --font-b: 800;
  --font-black: 900;
  --font-eblack: 950;
  --font-demi: 600;
}

/*
Futura PT, Medium-
Futura PT, Book-
Futura PT, Demi

*/
body,
html {
  font-family: "Ubuntu", sans-serif;
  line-height: 1;
  font-size: 18px;
  font-weight: var(--font-r);
  color: var(--p-color);
  margin: 0;
  padding: 0;
}

body.scroll-hidden {
  overflow: hidden;
}

* {
  font-family: "Ubuntu", sans-serif;
  box-sizing: border-box;
}

.d-none {
  display: none;
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-r);
  font-family: "Ubuntu", sans-serif;
  font-size: 3.1666666667rem;
  line-height: 3.2222222222rem;
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: 2rem;
  font-family: "Ubuntu", sans-serif;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 1.6666666667rem;
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1.3333333333rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 1.1666666667rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 0.8888888889rem;
}

p {
  color: var(--black);
  font-weight: var(--font-r);
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

.tags li {
  list-style: disc;
}

img {
  display: block;
  width: auto;
  height: auto;
}

svg {
  width: auto;
  height: auto;
}

.btn,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.btn {
  background-color: var(--pc);
  padding: 0.6666666667rem 1.7777777778rem;
  border-radius: 0.3333333333rem;
  border: unset;
  font-size: 1rem;
  font-weight: var(--font-b);
  color: var(--white);
  cursor: pointer;
}

.sec-title {
  font-size: 2rem;
  font-weight: var(--font-b);
  line-height: 1.4;
  margin-block: 0 1.9444444444rem;
}
.sec-title span {
  font-weight: var(--font-sb);
}

.btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn-container a {
  width: fit-content;
  display: block;
}

tldx-lmi-shadow-root {
  display: none;
}

@media (max-width: 1920px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 77.7777777778rem;
    margin: 0 auto;
    padding: 0 1.1111111111rem;
  }
}
@media (min-width: 1920px) {
  .container,
.container-lg,
.container-md,
