    margin: 0 auto;
    padding: 0 1.1111111111rem;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 0.8888888889rem;
}

/* ================= HEADER ================= */
.site-header {
  padding: 1.1111111111rem 0;
  transition: all 0.3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 11;
}
.site-header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.site-header .logo {
  font-size: 1.2222222222rem;
  font-weight: 700;
}
.site-header .nav a {
  margin: 0 0.8333333333rem;
  font-size: 0.8333333333rem;
  font-weight: 500;
  text-decoration: none;
  color: #222;
}
.site-header .nav a:hover {
  color: #000;
}
.site-header .auth {
  display: flex;
  align-items: center;
}
.site-header .auth button {
  font-size: 0.7777777778rem;
  border-radius: 1.1111111111rem;
  padding: 0.4444444444rem 0.8888888889rem;
  cursor: pointer;
}
.site-header .auth .btn-outline {
  background: transparent;
  border: 0.0555555556rem solid #ffcc00;
}
.site-header .auth .btn-primary {
  background: #ffcc00;
  border: none;
  margin-left: 0.5555555556rem;
}

/* ================= HERO ================= */
/* HERO */
.hero {
  padding-top: 5.5555555556rem;
  background: transparent linear-gradient(180deg, #fff4d3 65%, rgba(255, 255, 255, 0) 100%);
}
.hero .hero-title {
  margin-bottom: 1.1111111111rem;
}
.hero .hero-title h1 {
  font-size: 3.1111111111rem;
  line-height: 4.0555555556rem;
  font-weight: 700;
  color: #0b1f33;
}
.hero .hero-title h1 span {
  position: relative;
  z-index: 1;
}
.hero .hero-title h1 span:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0.3333333333rem;
  width: 100%;
  height: 0.7777777778rem;
  background: #ffcc00;
  z-index: -1;
}
.hero .hero-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 3.3333333333rem;
}

/* LEFT */
.service-tabs {
  width: 50%;
  position: relative;
  z-index: 10;
}
.service-tabs .service-tab {
  background: #fff;
  border-radius: 0.8888888889rem;
  padding: 1.2222222222rem;
  margin-bottom: 0.8888888889rem;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.4s ease;
}
.service-tabs .service-tab .tab-head {
  display: flex;
  gap: 0.5555555556rem;
  font-size: 1.0555555556rem;
  line-height: 1.3888888889rem;
  font-weight: 500;
  align-items: center;
}
.service-tabs .service-tab .tab-body {
  max-height: 0;
  opacity: 0;
  transform: translateY(0);
  pointer-events: none;
  overflow: hidden;
}
.service-tabs .service-tab .tab-body h2 {
  font-size: 1.7777777778rem;
  line-height: 2.3333333333rem;
  font-weight: 700;
  margin: 1.1111111111rem 0 0.5555555556rem;
  color: #0b1f33;
}
.service-tabs .service-tab .tab-body p {
  font-size: 0.7777777778rem;
  line-height: 1.1111111111rem;
  font-weight: 400;
  padding-bottom: 1.1111111111rem;
  color: #555;
}
.service-tabs .service-tab .tab-body .business-cta {
  flex-direction: row !important;
  gap: 1.1111111111rem !important;
  padding-bottom: 1.1111111111rem;
  display: flex;
  align-items: center;
  gap: 0px;
}
.service-tabs .service-tab .tab-body .business-cta img {
  font-size: 0.8888888889rem;
}
.service-tabs .service-tab .tab-body .business-btn {
  font-size: 0.7222222222rem;
  font-weight: 700;
  line-height: normal;
  padding-bottom: 0.5555555556rem;
  display: flex;
  gap: 0.2777777778rem;
  padding: 0;
}
.service-tabs .service-tab .tab-body .business-btn img {
  max-width: 0.8888888889rem;
  transition: transform 0.3s ease;
}
.service-tabs .service-tab .tab-body .business-btn:hover {
  text-decoration: underline;
}
.service-tabs .service-tab .tab-body .business-btn:hover img {
  transform: translateX(0.1111111111rem);
}
.service-tabs .service-tab .tab-body .actions {
  display: flex;
  flex-wrap: wrap;
  gap: 1.6666666667rem;
  flex-direction: column;
}
.service-tabs .service-tab .tab-body .actions .info-cta {
  display: flex;
  gap: 0.5555555556rem;
}
.service-tabs .service-tab .tab-body .actions button,
.service-tabs .service-tab .tab-body .actions .call {
  font-weight: 600;
  transition: all 0.3s ease;
}
.service-tabs .service-tab .tab-body .actions .call {
  display: inline-flex;
  align-items: center;
  gap: 0.4444444444rem;
  border: 0.1111111111rem solid #ffcc00;
  padding-left: 1.1111111111rem;
  padding-right: 0.2222222222rem;
  padding-top: 0.1666666667rem;
  padding-bottom: 0.1666666667rem;
  border-radius: 1.6666666667rem;
  text-decoration: none;
  color: #000;
  width: fit-content;
  font-size: 1.0555555556rem;
  line-height: 1.1111111111rem;
  font-weight: 900;
}
.service-tabs .service-tab .tab-body .actions .call strong {
  display: flex;
  gap: 0.5555555556rem;
}
.service-tabs .service-tab .tab-body .actions .call img {
  background-color: #ffcc00;
  padding: 0.2777777778rem;
  border-radius: 50%;
  max-width: 1.6666666667rem;
}
.service-tabs .service-tab .tab-body .actions .call:hover {
  background: #ffcc00;
  transform: translateY(-0.1111111111rem);
  box-shadow: 0 0.4444444444rem 1.1111111111rem rgba(255, 204, 0, 0.35);
}
.service-tabs .service-tab .tab-body .actions .call:hover img {
  background-color: #fff;
}
.service-tabs .service-tab .tab-body .actions .primary {
  background: #ffcc00;
  border: none;
  padding: 0.5555555556rem 1.2222222222rem;
  border-radius: 1.6666666667rem;
  border: 0.1111111111rem solid #ffcc00;
  cursor: pointer;
  font-size: 0.7222222222rem;
  font-weight: 700;
  line-height: 0.7222222222rem;
}
.service-tabs .service-tab .tab-body .actions .primary:hover {
  background: #fff;
  transform: translateY(-0.0555555556rem);
}
.service-tabs .service-tab .tab-body .actions .outline {
  background: transparent;
  border: 0.1111111111rem solid #ffcc00;
  padding: 0.5555555556rem 1.2222222222rem;
  border-radius: 1.6666666667rem;
  font-size: 0.7222222222rem;
  font-weight: 700;
  line-height: 0.7222222222rem;
  cursor: pointer;
}
.service-tabs .service-tab .tab-body .actions .outline:hover {
  background: #ffcc00;
  transform: translateY(-0.0555555556rem);
}
.service-tabs .service-tab .tab-body .actions .link {
  text-decoration: none;
  font-size: 0.7777777778rem;
  color: #000;
  position: relative;
}
.service-tabs .service-tab .tab-body .actions .link:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -0.2222222222rem;
  width: 0;
  height: 0.1111111111rem;
  background: #ffcc00;
  transition: width 0.3s ease;
}
.service-tabs .service-tab .tab-body .actions .link:hover:after {
  width: 100%;
}
.service-tabs .service-tab.active {
  border-left-color: #ffcc00;
  box-shadow: 0 1.1111111111rem 2.2222222222rem rgba(0, 0, 0, 0.12);
  position: relative;
  z-index: 20;
}
.service-tabs .service-tab.active .tab-body {
  max-height: 16.6666666667rem;
  opacity: 1;
  transform: translateY(0);
  pointer-events: auto;
  transition: max-height 0.6s ease, opacity 0.6s ease;
}

/* RIGHT */
.service-images {
  position: relative;
  width: 50%;
}
.service-images .service-image {
  position: absolute;
  inset: 0;
  opacity: 0;
  transform: translateX(2.7777777778rem);
  transition: all 0.5s ease;
}
.service-images .service-image img {
  width: 100%;
}
.service-images .service-image.active {
  position: relative;
  opacity: 1;
  transform: translateX(0);
}

/* Progress Bar */
.service-tab .progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  border-radius: 0 0 0.8888888889rem 0.8888888889rem;
}
.service-tab .progress-bar .progress-fill {
  height: 100%;
  width: 0%;
  background: #ffcc00;
  border-radius: 0 0 0.8888888889rem 0.8888888889rem;
}

